import React from 'react';
import "@fontsource/roboto"
import "@fontsource/open-sans"
import "@fontsource/merriweather"
import { Link } from "gatsby"

import * as layoutStyles from './Layout.module.css'
import './Layout.reset.css'

const navItems = [
    {
        name: 'About',
        path: '/'
    },
    {
        name: 'R√©sum√©',
        path: '/resume'
    },
    {
        name: 'Tinker-Box',
        path: '/tinkerbox'
    },
    {
        name: 'Videos',
        path: '/videos'
    },
    {
        name: 'Misc',
        path: '/misc'
    },
]

const Layout = (props) => {
    return (
        <div className={layoutStyles.base}>
            <header className={layoutStyles.header}>

                <nav className={layoutStyles.mobileNavTop}>
                    <ul>
                        {navItems.map(item => (<li key={item.name}><Link to={item.path}>{item.name}</Link></li>))}
                    </ul>
                    <span> - MENU - </span>
                </nav>

                <div className={layoutStyles.logoTop}>
                    <h1>
                        <Link to="/">Dipankar Medhi</Link>
                    </h1>
                </div>

                <nav className={layoutStyles.navTop}>
                    <ul>
                        {navItems.map(item => (<li key={item.name}><Link to={item.path}>{item.name}</Link></li>))}
                    </ul>
                </nav>

                <div className={layoutStyles.topDivider}></div>
            </header>

            <aside className={layoutStyles.sidebar}>
                <nav>
                    <ul>
                        {navItems.map(item => (<li key={item.name}><Link to={item.path}>{item.name}</Link></li>))}
                    </ul>
                    <span> - MENU - </span>
                </nav>
            </aside>

            <main role='main' className={layoutStyles.main}>
                {props.children}
            </main>

            <footer className={layoutStyles.footer}>
                Welcome to my Gatsby Site
            </footer>
        </div>
    );
};

Layout.propTypes = {};

export default Layout;